import Grid from './grid.js';
import primitives from './primitives.js';
import { SelectionState, MandalaState } from './state.js';
import * as uiJs from './ui.js';
import { createSerialIDGenerator } from './helpers.js';

/* Setup state */

let order = 8;
let invert  = false;
let showIDs = false;
const parameters = [
  { name: 'distance',     min: '0', max: '1',   step: '0.001', initial: '0.9' },
  { name: 'angle',        min: '-180', max: '180', step: '0.001', initial: '0' },
  { name: 'rotation',     min: '0', max: '360', step: '0.001', initial: '0' },
  { name: 'scale',        min: '0', max: '1',   step: '0.001', initial: '0.25' },
  { name: 'multiplicity', min: '0', max: '1024',   step: '1', initial: '1' }
];
const [ canvasWidth, canvasHeight ] = [ 800, 800 ];
const mainCanvas = document.querySelector('#main-canvas');
[ mainCanvas.width, mainCanvas.height ] = [ canvasWidth, canvasHeight ];

const selectionState = new SelectionState();
let mandalaState   = new MandalaState();

let getID = createSerialIDGenerator();

let grid = new Grid();

/* Image */

/* Clear the canvas */

function clear(canvas) {
  const context = canvas.getContext('2d');
  context.fillStyle = invert ? 'black' : 'white';
  context.fillRect(0, 0, canvas.width, canvas.height);
}

/* Events */

/* Top-level drawing function */

let tick = 0;

function draw() {

  clear(mainCanvas, invert);

  grid.drawImageTo(mainCanvas);

  mandalaState.drawImageTo(mainCanvas, invert);
}

/* Setup UI when the page has loaded */

window.addEventListener('load', function () {
  window.requestAnimationFrame(draw);

  uiJs.setupGridOrderControl((gridOrder) => {
    const answer = confirm('This will erase the existing mandala on the canvas. Do you want to continue?');

    if (answer) {
      const prevVisible = grid.visible;

      grid = new Grid(gridOrder, prevVisible);
      order = gridOrder;

      mandalaState = new MandalaState(gridOrder);
      uiJs.clearUsedPrimitives();
      uiJs.showControls(false);

      getID = createSerialIDGenerator(1);

      mandalaState.showIDs(showIDs);

      changeSelection(null);

      requestAnimationFrame(draw);
    }
  })

  uiJs.setupGridToggle((flag) => {
    grid.visible = flag;
    requestAnimationFrame(draw);
  });

  uiJs.setupSaveButton(() => {
    const saveCanvas = document.createElement('canvas');

    saveCanvas.width  = canvasWidth;
    saveCanvas.height = canvasHeight;

    mandalaState.drawImageTo(saveCanvas);

    saveCanvas.toBlob(blob => {
      const downloadLink = document.createElement('a');
      const downloadURL  = URL.createObjectURL(blob);
      
      downloadLink.href = downloadURL;
      downloadLink.download = 'mandala.png';
      downloadLink.classList.add('hidden');

      document.body.appendChild(downloadLink);
      downloadLink.click();
      document.body.removeChild(downloadLink);

      URL.revokeObjectURL(downloadURL);
    });
  });

  uiJs.setupSaveSerialButton(() => {
    const blob = new Blob(
      [ mandalaState.getPrimitiveStateAsJSON() ],
      { type: 'application/json', endings: 'native' }
    );

    const downloadLink = document.createElement('a');
    const downloadURL  = URL.createObjectURL(blob);
      
    downloadLink.href = downloadURL;
    downloadLink.download = 'mandala.json';
    downloadLink.classList.add('hidden');

    document.body.appendChild(downloadLink);
    downloadLink.click();
    document.body.removeChild(downloadLink);

    URL.revokeObjectURL(downloadURL);
  });

  uiJs.setupLoadSerialButton(() => {
    const answer = confirm('This will erase the existing mandala on the canvas. Do you want to continue?');

    if (answer) {
      const fileInput = document.querySelector('#hidden-file-input');

      fileInput.addEventListener('input', (ev) => {
        const file = ev.target.files[0];
        
        if (file.type === 'application/json') {
          const fileReader = new FileReader();

          fileReader.addEventListener('load', (ev2) => {
            try {
              const jsonObj = JSON.parse(ev2.target.result);

              if (!validateJSON(jsonObj)) {
                alert('The object in JSON file does not seem to have proper structure.');

              } else {
                const state = createMandalaStateFromJSON(jsonObj);

                if (state === null || jsonObj.order < 3) {
                  alert('The JSON file does not state a valid symmetry order');
                } else {
                  const prevVisible = grid.visible;
                
                  grid  = new Grid(jsonObj.order, prevVisible);
                  order = jsonObj.order;
                  invert = jsonObj.invert;
                  uiJs.updateInvertControl(invert);

                  mandalaState = state;
                  uiJs.clearUsedPrimitives();
                  mandalaState.showIDs(showIDs);
                  mandalaState.invertColor(invert);
                
                  mandalaState.primitiveGroup.forEach((val, key) => {
                    function selectHandler() { changeSelection(key); }

                    function deleteHandler() {
                      mandalaState.removePrimitive(key);
                      changeSelection(null);
                    }

                    uiJs.addUsedPrimitive(val.primitive, key, selectHandler, deleteHandler);
                  });

                  uiJs.showControls(false);

                  changeSelection(null);

                  requestAnimationFrame(draw);
                }
              }

            } catch (error) {
              console.error(error);
              alert('Error occured on parsing the file. Check the console for more details.');
            }
          });

          fileReader.addEventListener('error', () => {
            alert('Error occured on reading the file.');
          });

          fileReader.readAsText(file);
        } else {
          alert(`The file ${file.name} does not seem to be a valid JSON file.`);
        }
      });

      fileInput.click();
    }
  });

  uiJs.setupInvertControl((status) => {
    invert = status;
    mandalaState.invertColor(status);

    requestAnimationFrame(draw);
  });

  uiJs.setupIDCheckbox((status) => {
    showIDs = status;
    mandalaState.showIDs(status);

    requestAnimationFrame(draw);
  });

  for (const id in primitives) {
    function clickHandler() {
      const symbol = getID();

      mandalaState.addPrimitive(symbol, primitives[id]);
      changeSelection(symbol);

      return symbol;
    }

    function selectHandler(symbol) { changeSelection(symbol); }

    function deleteHandler(symbol) {
      mandalaState.removePrimitive(symbol);
      changeSelection(null);
    }

    uiJs.addPrimitiveButton(primitives[id], clickHandler, selectHandler, deleteHandler);
  }

  parameters.forEach(({ name, min, max, step, initial }) => {
    function inputHandler(value) {
      const props = {};
      props[name] = value;
      
      mandalaState.updatePrimitiveState(selectionState.getState(), props);
      uiJs.updateControlsFor(name, value);
      
      requestAnimationFrame(draw);
    }
  
    uiJs.setupControlsFor(name, { min, max, step, initial }, inputHandler);
  });

  uiJs.setupFlipToggle((flag) => {
    mandalaState.updatePrimitiveState(selectionState.getState(), { flip: flag });
    uiJs.updateFlipToggle(flag);

    requestAnimationFrame(draw);
  });

  uiJs.setupInvertToggle((flag) => {
    mandalaState.updatePrimitiveState(selectionState.getState(), { invert: flag });
    uiJs.updateInvertToggle(flag);

    requestAnimationFrame(draw);
  });
});

/* Helpers */

function changeSelection(symbol) {
  selectionState.changeState(symbol);

  uiJs.showControls(symbol !== null);

  if (symbol !== null) {
    const props = mandalaState.getPrimitiveState(symbol).props;

    parameters.forEach(parameter => {
      uiJs.updateControlsFor(parameter.name, props[parameter.name])
    });
    uiJs.updateFlipToggle(props.flip);
    uiJs.updateInvertToggle(props.invert);
  }

  requestAnimationFrame(draw);
}

function createMandalaStateFromJSON(jsonObj) {
  if (jsonObj.order === undefined) return null;

  const state = new MandalaState(jsonObj.order);
  
  getID = createSerialIDGenerator(1);

  if (jsonObj.primitiveList) {
    for (const { id, polarRadius, polarAngle, selfRotation, size, multiplicity, flip, invert } of jsonObj.primitiveList) {
      state.addPrimitive(getID(), primitives[id], {
        distance: polarRadius,
        angle:    bringWithinRange(polarAngle, -180, 180),
        rotation: bringWithinRange(selfRotation, 0, 360),
        scale:    size,
        multiplicity, flip, invert
      });
    }
  }

  return state;
}

function validateJSON(loadedJSON) {

  if (loadedJSON.order === undefined || loadedJSON.primitiveList === undefined || loadedJSON.invert === undefined) return false;

  const props = [ 'id', 'polarRadius', 'polarAngle', 'selfRotation', 'size', 'multiplicity', 'flip', 'invert' ];

  const floats = [
    { name: 'polarRadius', min: 0, max: 1 },
    { name: 'size', min: 0, max: 1 }
  ];
  const numbers = [
    { name: 'polarAngle' },
    { name: 'selfRotation' }
  ];
  const booleans = [
    { name: 'flip' },
    { name: 'invert' }
  ];

  for (const primitive of loadedJSON.primitiveList) {
    for (const prop of props) if (!prop in primitive) return false;

    for (const { name, min, max } of floats) {
      const value = primitive[name];
      if (!Number.isFinite(value) || value < min || value > max) return false;
    }

    for (const { name } of numbers) {
      const value = primitive[name];
      if (!Number.isFinite(value)) return false;
    }

    for (const { name } of booleans) {
      const value = primitive[name];
      if(value !== true && value !== false) return false;
    }

    const multiplicity = primitive['multiplicity'];
    if (!Number.isInteger(multiplicity) || multiplicity < 0 || multiplicity > 1024) return false;

    const id = Number.parseInt(primitive['id']);
    if (!Number.isInteger(id) || id < 0 || id > 112) return false;
  }

  return true;
}

function bringWithinRange(val, min, max) {
  const range = max - min;
  const shiftedVal = val - min;
  const inRangeVal = shiftedVal - (Math.floor(shiftedVal / range) * range);
  const finalInRangeVal = inRangeVal + min;

  return finalInRangeVal;
}